import { Teksten } from "../../../content";
import { HeadingH3Atom, SpacerAtom } from "../../atoms";
import { DefaultLayout } from "../../layouts";
import { BackgroundBlockMolecule } from "../../molecules";
import {
  FooterOrganism,
  HeaderOrganism,
  OverSpecialistenOrganism,
  TeamSectionOrganism,
} from "../../organisms";

export default function OverScreen() {
  return (
    <DefaultLayout
      header={<HeaderOrganism />}
      body={
        <>
          <SpacerAtom space={25} />
          <TeamSectionOrganism />
          <OverSpecialistenOrganism />
          <BackgroundBlockMolecule
            children={
              <HeadingH3Atom children={Teksten.ontstaan.text} color="zand" />
            }
            background={"oranje"}
          />
          {/* <MissieVisieWerkwijzenOrganism /> */}
          {/* <SloganOrganism slogan={Teksten.slogan1.title} /> */}
        </>
      }
      footer={<FooterOrganism />}
    />
  );
}
